
#programs article {
  background-color: white;
}

#programs article img {
  position: relative;
  min-width: 100%;
}

/* заголовок в article */
#programs article a {
  display: block;
  background-color: rgba($color-secondary, 0.75);
  &:hover {
    color: white;
    background-color: rgba($color-secondary, 0.3);
  }
}
#programs article a{
  position: relative;
  padding: 0 1em;
  margin: 0;
  margin-top: -1.5em;
  font-size: 0.9em;
  font-weight: 600;
  line-height: 1.5em;
  color: white;

  // стрелочка справа от заголовка
  &::after {
    float: right;
    font-family: icons;
    content: 'c';
  }
}
#programs article p {
  padding: 1em;
  margin: 0;
  // font-size: 0.65em;
  font-weight: 300;
}