.line {
  margin: 0;
  margin-top: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  border-bottom: 0.4rem solid;
}
.line.color-primary{
  color: $color-primary;
  border-color: $color-primary;
}
.line.color-secondary{
  color: $color-secondary;
  border-color: $color-secondary;
}

.line.otzivy {
  border: none;
}

// стрелочка у отзывов
.icon-arrow:after {
  font-family: icons;
  content: 'c';
  font-size: 0.6em;
  padding-left: 0.6em;
}