#slider {
  position: relative;
  z-index: 10;
}

// второстемпенное меню на главной странице
@media only screen and (min-width: calculate-rem(1200px)) {
  #slider  {
    margin-bottom: -$height-secondry-menu;
  }
}

@media only screen and (max-width: calculate-rem(600px)) {
  #slider  {
    // display: none;
  }
}
#slider .item img {
  display: block;
  width: 100%;
  // min-height: 100%;
}

/*****************************************
            навигация слайдера
==========================================*/
// сдвинем круглишки с навигацией
#slider .owl-dots {
  margin: auto;
  position: absolute;
  left: 0; right: 0;
  // подвинем вверх
  transform: translate(0, -100%);
  -webkit-transform: translate(0, -100%); /* Safari */
  z-index: 20;
}

// навигационные точки выше когда все прыгнет вверх
@media only screen and (min-width: calculate-rem(1200px)) {
  #slider .owl-dots  {
    margin-top: -$height-secondry-menu;
  }
}

#slider .owl-dot {
  margin: calculate-rem(10px);
  padding: calculate-rem(4px);
  box-sizing: border-box;
  border: calculate-rem(2px) solid white;
  border-radius: 100%;
  background: transparent;
}

#slider .owl-dot.active {
  box-sizing: border-box;
  border-width: calculate-rem(4px);
  transform: translate(0, 10%);
  -webkit-transform: translate(0, 10%); /* Safari */
}
#slider .owl-dot span {
  height: calculate-rem(6px);
  width: calculate-rem(6px);
  margin: 0;
  background: transparent;
}

/*****************************************
             текст слайдера
==========================================*/

.slider-text {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%); /* Safari */
  display: inline;
  text-align: center;
  color: white;
}
// сдивенм чуть-чуть что из-за нижнего меню на слайдере
@media only screen and (min-width: calculate-rem(1200px)) {
  .slider-text {
    transform: translate(-50%, -80%);
    -webkit-transform: translate(-50%, -80%); /* Safari */
  }
}

.slider-text h3 {
  margin: 0;
  padding: 0 calculate-rem(40px);
  font-family: 'Russo One', sans-serif;
  font-size: calculate-rem(36px);
  font-size: 2.5vw;
}

.slider-text .text {
  padding-top: calculate-rem(10px)/2;
  padding-bottom: calculate-rem(10px);
  min-height: 5vw;
  border-top: calculate-rem(5px) solid white;
  border-top: 0.3vw solid white;
  background-color: rgba($color-primary, .75);
}

.slider-text .text p,
.slider-text .text ul {
  margin: 0;
  padding: 0;
  padding: 0 calculate-rem(40px);
  font-size: calculate-rem(18px);
  font-size: 1.2vw;
}