// растянем лого на всю ширину колонки
.logo img {
  width: 100%;
  max-width: calculate-rem(220px)
}

header a {
  position: relative;
}

.flexbox-container {
  padding: calculate-rem(10px) 0;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: calculate-rem(640px)) {
  // .flexbox-container { display: block; }
}


/******************************************
              иконка телефона
==========================================*/
header .tel {
  vertical-align: middle;
  text-align: center;
  color: $color-secondary;
  font-size: calculate-rem(20px);
  font-weight: bold;
  line-height: 200%;
  vertical-align: middle;
  // hacks fo middle icon :(
}
.tel .icon-tel {
  display: inline-block;
  width: 0.8em;
  font-size: 0.8em;
  margin-right: calculate-rem(8px);
  vertical-align: middle;
}
/******************************************
              иконка письмо
==========================================*/
header .letter {
  vertical-align: middle;
  color: $color-primary;
  font-size: calculate-rem(12px);
  line-height: 100%;
  vertical-align: middle;
  &::before {
    margin-right: calculate-rem(5px);
    vertical-align: middle;
    font-size: 1.2em;
  }
}

header .letter {
  // display: inline-block;
}

.letter .icon-letter {
  display: inline-block;
  width: 0.8em;
  font-size: 0.8em;
  margin-right: calculate-rem(8px);
  vertical-align: middle;
}

/******************************************
            социальные иконки
==========================================*/
// социальные иконки в хедере
header .social{
  float: right;
  width: 100%;
  vertical-align: middle;
  font-size: calculate-rem(22px);
}

.social ul {
  margin: 0;
  line-height: 100%;
  text-align: right;
}

.social li {
  display: inline-block;
  margin-right: 0;
}

.social .icon-vk,
.social .icon-facebook,
.social .icon-youtube, {
  display: inline-block;
  width: 0.8em;
  font-size: 0.8em;
  margin-right: calculate-rem(2px);
  vertical-align: middle;
}
