@charset "UTF-8";

// добавим компоненты foundation
@import 'normalize';
@import 'foundation-custom/foundation';

// переменные
@import 'variables';
@import 'functions';

// миксины
@import 'mixins';

// импортируем иконки
@import 'components/icons';

// секция с лого
@import 'components/header';

// секции с основным меню
@import 'components/main-menu';

// импортируем тему слайдера и секцию со слайдером
@import 'owl.carousel';
// @import 'owl.lazyload';
@import 'owl.theme.default';
@import 'components/slider';

// секция со второстепенным меню
// меню с иконками снизу слайдера
@import 'components/secondary-menu';

// секция с акциями
@import 'components/actions';

// подчеркнутый текст
@import 'components/line-text';

// секция с программами
@import 'components/programs';

// форма с выбором программ
@import 'components/form-program';

// секция с отзывами
@import 'components/feedbacks';

// секция с фото
@import 'components/fotos';

// секция с копирайтом
@import 'components/footer';

// секция с виджетом
// @import 'components/widget';

// мобильное меню
@import 'components/mobile-menu';

/******************************************
           внутренние страниц
===========================================*/
// секция со второстепенным меню
// на внутренних страницах
@import 'components/aside-menu';
@import 'components/page-title';
@import 'components/content';



html, body { height: 100% }

body {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
}

.overflow {
  display: block;
  overflow: hidden;
}
p {
  font-size: calculate-rem(12px);
}
ul {   padding: 0; };

// цвет ссылок по умолчанию
a {
  color: $color-primary;
  text-decoration: none;
}
a:hover { color: $color-secondary; }


.bg-grey {
  // background-color: #f0eaf1;
  background-color: lightgrey;
}

.bg-texture {
  background-image: url('../images/bg-texture.jpg');
}

.bg-white-transparent {
  background-color: rgba(255,255,255,0.5);
}

.bg-feedbacks {
  background-image: url('../images/bg-feedbacks.jpg');
  background-size: cover;
}

.conteiner-article {
  padding: 1.5em 0;
}

.widget {
  min-height: 200px;
  border: 2px solid grey;
  padding: 10px;
}

