
#feedbacks article {
  background-color: white;
}

/* шапка у отзыва */
#feedbacks header {
  height: auto;
  color: white;
  background-color: $color-primary;
}

#feedbacks article {
  position: relative;
}

/* аватар у отзыва*/
#feedbacks article img {
  position: absolute;
  float: left;
  height: calculate-rem(70px);
  border: calculate-rem(5px) solid $color-primary;
  border-radius: 50%;
  top: calculate-rem(-9px);
  left: 0;
}

/* контейнер с заголовком отзыва */
#feedbacks article hgroup {
  padding: calculate-rem(10px);
  padding-left: calculate-rem(70px+5px);
}

/* автор отзыва */
#feedbacks article h3 {
  color: white;
  font-size: 0.75em;
  margin: 0;
}

/* обрезаем строку и ставим многоточие */
.clip {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
/* тема отзыва */
#feedbacks article h4 {
  font-weight: 500;
  color: white;
  font-size: 0.70em;
  margin: 0;
}

#feedbacks article p {
  height: calculate-rem($height-feedbacks);
  padding: calculate-rem(10px);
  margin: 0;
  overflow: hidden;
  // font-size: calculate-rem(10px);
  font-weight: normal;
}

#feedbacks footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  text-align: center;
  // белый градиент
  background: rgba(255,255,255,0);
  background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(255,255,255,0)), color-stop(72%, rgba(255,255,255,1)), color-stop(100%, rgba(255,255,255,1)));
  background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
  background: -o-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
  background: -ms-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
  background: linear-gradient(to bottom, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 72%, rgba(255,255,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff', GradientType=0 );
}

#feedbacks article a{
  font-family: 'icons';
  // line-height: 2em;
  font-size: calculate-rem(20px);
}
