#main-menu {
  // height: $height-main-menu;
  // прозрачный цвет фона
  background-color: rgba($color-primary, 0.8);
  z-index: 200;
}
// без фона на внутренних страницах
#main-menu.inside {
  background-color: transparent;
}

// хак что бы засунуть слайдер под меню
#main-menu {
  position: absolute;
  left:0; right: 0;
}
// второстемпенное меню на главной странице
@media only screen and (max-width: calculate-rem(640px)) {
  #main-menu {
    position: relative;
    background-color: $color-primary;
  }
}
/*****************************************
       меню с маркерами под лого
==========================================*/
#main-menu ul {
  margin: 0;
  // без этой высоты li становиться на 1px больше (((
  height: $height-main-menu;
  font-size: 0.7em;
}
#main-menu li {
  display: inline-block;
  line-height: $height-main-menu;

  // маркеры в главном меню
  &:after {
    color: $color-secondary;
    content: "\25CF";
    margin: 0 .8em;
    font-size: 1.2em;
  }
  // последний маркер удалим
  &:last-child:after {
    display: none;
  }
}
#main-menu a {
  color: white;
  &:hover {
    text-decoration: underline;
  }
}

/*****************************************
           форма поиска под лого
==========================================*/
#main-menu form {
  position: relative;
  line-height: $height-main-menu;
  margin: 0;
}
#main-menu input {
  display: inline-block;
  margin: 0;
  background-color: transparent;
  border-color: $color-primary-light;
  color: white;

  /* стилизуем placeholder */
  &::-webkit-input-placeholder { color: $color-primary-light; }
  &::-moz-placeholder          { color: $color-primary-light; } /* Firefox 19+ */
  &:-moz-placeholder           { color: $color-primary-light; } /* Firefox 18- */
  &:-ms-input-placeholder      { color: $color-primary-light; }
}

/* кнопка поиска */
#main-menu button {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0; bottom: 0; right: 0;
  background-color: transparent;
  color: $color-primary-light;
}

