

/*************************************************
                лента бекграунд
================================================*/
#actions {
  background-repeat: no-repeat;
  background-position: 100% 95%;
  background-size: 100% 51%;
}

#actions img { min-width: 100% }

// показываем ленту только на large
@media only screen and (min-width: calculate-rem(1200px)) {
  #actions {
    background-image: url('../images/actions/lenta.svg');
  }
}

#actions article {
  position: relative;
  bottom: 0;
  display: block;
  text-align: center;
  font-size: calculate-rem(16px);
  font-weight: 500;
}

/* выровняем плашки с акциями по низу */
#actions article {
  padding-top: calculate-rem(22px);
  margin-top: calculate-rem(5px)
}

#actions a {
  display: block;
}
#actions article.spec {
  padding-top: 0;
}

#actions header {
  height: auto;
}

#actions article h4 {
  display: block;
  position: relative;
  margin: 0 calculate-rem(30px);
  color: white;
  margin-top: calculate-rem(-50px);
  padding: 0;
  height: calculate-rem(50px);
  font-family: 'Russo One', sans-serif;
  font-size: 120%;
  font-weight: 400;
  line-height: 100%;
}

// @media only screen and (min-width: calculate-rem(1200px)) {
//   #actions article h4 {
//     font-size: 90%;
//   }
// }
/*************************************************
             скругяем углы фото
================================================*/
#actions img {
  border-top-left-radius: calculate-rem(8px);
  border-top-right-radius: calculate-rem(8px);
}

/*************************************************
            плашка-домик в акциях
================================================*/

.container-plashka-center {
  display: block;
  width: 80%;
  position: relative;
  height: calculate-rem(65px);
  margin: 0 auto;
  margin-top: calculate-rem(-65px);
  background-image: url('../images/plashka-center.svg');
  opacity: .8;
  background-size: 100% 100%;
}

a:hover .container-plashka-center {
  opacity: .5;
}

