/*************************************************
    плашка с вырезанным треугольником в акциях
================================================*/

.container-plashka {
  height: calculate-rem(10px);
  width: 100%;
  overflow: hidden;
  position: relative;
  // один пиксель для Оперы!
  margin-top: calculate-rem(-9px);
  margin-bottom: 0;
  background-image: url('../images/plashka.svg');
  background-color: rgba($color-secondary, .8);
  background-size: 100%;
}

a:hover .container-plashka {
  background-color: transparent;
}

/*************************************************
       отобразить mirror horizontal
================================================*/
.flip {
  -moz-transform: scaleX(-1);    /* Gecko */
  -o-transform: scaleX(-1);      /* Operah */
  -webkit-transform: scaleX(-1); /* webkit */
  transform: scaleX(-1);         /* standard */
  filter: FlipH;                 /* IE 6/7/8 */
}

/*************************************************
         нижняя плашка с описанием акции
================================================*/

#actions p {
  margin: 0;
  padding: 0 calculate-rem(5px);
  font-size: inherit;
  padding-top: calculate-rem(5px);
  padding-bottom: calculate-rem(15px);
  color: white;
  background-color: $color-primary;
  border-bottom: calculate-rem(6px) solid $color-primary;
}

// по центру шрифт больше, центруем паддингами по другому
#actions .spec p {
  padding: calculate-rem(10px) 0;
}

#actions .spec p {
  margin-top: calculate-rem(-1px);
  color: $color-secondary;
  font-family: 'Russo One', sans-serif;
  font-size: 150%;
  font-weight: 500;
  background-color: white;
  border-color: $color-secondary;
}

@media only screen and (max-width: calculate-rem(1200px)) {
  #actions .spec p {
    color: $white;
    background-color: $color-secondary;
  line-height: 1.35;
  }
}
