/*************************************************
                 secondary-menu
 ================================================*/

#secondary-menu {
  position: relative;
  // height: $height-secondry-menu;
  // прозрачный цвет фона
  background-color: $color-primary;
  z-index: 300;
}
// прозрачное меню когда прыгнет на фото
@media only screen and (min-width: calculate-rem(1200px)) {
  #secondary-menu {
    background-color: rgba($color-primary, 0.8);
  }
}
/*****************************************
       элемент ссылки с иконкой
==========================================*/

#secondary-menu a {
  height: $height-secondry-menu;
  display: inline-block;
  width: 100%;
  padding: $height-secondry-menu/6 0;
  color: white;
  font-size: calculate-rem(12px);
  line-height: 120%;
  text-align: center;
  &:hover {
    background-color: $color-primary;
    background-color: rgba($color-secondary, 0.8);
  }
}

#secondary-menu i {
  display: block;
  font-size: calculate-rem($icon-secondry-menu);
  line-height: calculate-rem($icon-secondry-menu);
}

// полоски между кнопками меню
.container-secondary-menu_i {
  border-left: 1px solid lighten($color-primary, 10%);
  border-right: 1px solid $color-primary;
  &.no-left  { border-left: none }
  &.no-right { border-right: none }
}

#secondary-menu a:hover .container-secondary-menu_i {
  border: none;
}
