}

.bg-feedbacks #footer a {
  color: inherit;
  &:hover {
    color: lightgrey;
  }
}

// оранжевая полоса в футере на второстепенных стрвницах
.bg-texture #footer {
  margin-top: calculate-rem(20px);
  border-top: 2px solid $color-secondary;
  color: $color-primary;
  font-weight: 500;
}

.bg-texture p {
    font-size: calculate-rem(16px);
}

.bg-texture ul.social li a{
  font-size: calculate-rem(24px);
}

@media only screen and (max-width: calculate-rem(640px)) {
  // отцентруем текст при мобильном виде
  #footer p {
    text-align: center;
    font-size: 1rem;
  }
  #footer .social ul {
    text-align: center;
    font-size: calculate-rem(36px);
  }
  #footer .social ul li {
    margin: 0 1rem;
  }
}