


.aside-menu_i li {
  background-color: $color-primary;
  border-top: 1px solid lighten($color-primary, 10%);
  border-bottom: 1px solid darken($color-primary, 10%);
  color: white;
  font-size: calculate-rem(10px);
  // transition: all 1s ease 1s;
  // line-height: 1.5;
  &:hover {
    position:relative;
    left:-10px;
    // transform: translate(0, 10px;)
    background-color: $color-secondary;
    border-color:$color-secondary;
  }
}

// слева меню оранжевая полоска
.aside-menu_i a {
  border-left: 5px solid $color-primary;
}

// слева меню синяя полоска которую не видно
.aside-menu_i .active a,
.aside-menu_i  a:hover {
  border-left-color:$color-secondary;
}

.aside-menu_i a {
  display: block;
  color: inherit;
  line-height: 2*calculate-rem($icon-aside-menu);
  &:hover { color: inherit; }
}

.aside-menu_i i {
  display: inline-block;
  vertical-align: middle;
  width: calculate-rem($icon-aside-menu)*2;
  padding: 0 calculate-rem($icon-aside-menu)/2;
  line-height: 100%;
  font-size: calculate-rem($icon-aside-menu);
}

.aside-menu_i span {
  vertical-align: middle;
  display: inline-block;
  line-height: 120%;
}
