.mobile-menu {
  width: 100%;
  margin: 0.625rem; }

.mobile-menu input + label {
  margin: 0; }

.mobile-menu_list-w {
  border: 0.0625rem #313131 solid;
  display: none;
  z-index: 9999;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: #4A4A4A;
  margin: 0;
  overflow-y: auto; }

.mobile-menu_list-w ul {
  margin: 0; }

.mobile-menu_list {
  transition: all 0.4s; }

.mobile-menu_list li {
  list-style: none; }
  .mobile-menu_list li:hover {
    background-color: grey; }

.mobile-menu_list > li > a {
  display: block;
  text-decoration: none;
  font-size: 1rem;
  font-family: 'Roboto', sans-serif;
  color: #fff;
  line-height: 4.5rem;
