
/*****************************************
        форма с выбором программ
==========================================*/

form[name="program"] {
  padding: calculate-rem(20px);
  display: block;
  font-size: 1rem;
  font-weight: 500;
  border: calculate-rem(3px) solid $color-secondary;
  border-radius: calculate-rem(5px);
  background-color: white;
}
form[name="program"] label {
  font: inherit;
  cursor: default;
  padding: calculate-rem(10px) 0;
  color: $color-secondary;
}
form[name="program"] .btn {
  margin: 0;
  min-width: 33.333%;
  padding: calculate-rem(10px) calculate-rem(20px);
  font: inherit;
  line-height: 100%;
  border-radius: calculate-rem(5px);
  background-color: $color-secondary;
  &:hover {
    background-color: darken($color-secondary, 5%);
  }
}
form[name="program"] select {
  line-height: 100%;
  background-color: transparent;
  font-size: calculate-rem(14px);
  border: calculate-rem(1px) solid $color-secondary;
  border-radius: calculate-rem(5px);
  cursor: pointer;
  // стилизуем стрелку для select
  // background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) no-repeat 98%;
  &:after {
    background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) no-repeat 98%;
  }
}

form[name="program"] option {
  padding: 5px;
  border-top: 1px solid lightgray;
  -webkit-appearance: none;
}

form[name="program"] .text-center {
  font: inherit;
  text-align: center;
}


